
.key{height:100%;border:1px solid #282828
}
div.active-key{background:#ff8c8c
}
.black-key{position:absolute;top:0px;width:calc(100% / 30);height:65%;background:#282828
}
.white-key{width:8%;width:calc(100% / 15);background:whitesmoke
}
@media (max-width: 500px){
.key{width:100%
}
.black-key{height:calc(100% / 30);width:65%;-ms-flex-item-align:end;align-self:flex-end
}
.white-key{height:calc(100% / 15)
}
}



