
.wheel-container {
    position: relative;
    height: 100%;
    width: 10%; 
    border: 4px solid lightgray;
    overflow-y: hidden;
}
.wheel {
    width: 100%;
    height: 45%;
    background: black;
}
.handle {
    background: #2f2f2f;
    height: 10%;
    width: 100%;
}



